// import Vue from 'vue'
import Vuex from 'vuex'
// import data from './data'
// import demo from './demo'
import reqData from "../reqData.js"
// Vue.use(Vuex);


const state = {
  company:sessionStorage.getItem("company"),
  userName:sessionStorage.getItem('username'),
  word:sessionStorage.getItem('word'),
  word1:[sessionStorage.getItem('word1')],
  leftNav:JSON.parse(sessionStorage.getItem('leftNav')),
  WordCloud:JSON.parse(sessionStorage.getItem("WordCloud")),
  question1:JSON.parse(sessionStorage.getItem("yidianAdd")),
  question2:JSON.parse(sessionStorage.getItem("caiwuAdd")),
  question3:JSON.parse(sessionStorage.getItem("cihuiAdd")),
  userJifen:sessionStorage.getItem("userJifen"),
  userLishiJifen:sessionStorage.getItem("userLishiJifen"),
  userJifenText:sessionStorage.getItem("userJifenText"),
  faguiNode:sessionStorage.getItem('word')+',',
  xinwenNode:sessionStorage.getItem('word')+',',
  faguiList:JSON.parse(sessionStorage.getItem('faguiList')),
  xinWenList:JSON.parse(sessionStorage.getItem('xinwenList')),
  faguiCWList:JSON.parse(sessionStorage.getItem('faguiCWList')),
  xinWenCWList:JSON.parse(sessionStorage.getItem('xinwenCWList')),
};
 
const mutations = {
  updateCompany(state){
    state.company=sessionStorage.getItem("company");
  },
  updateUser(state){
    state.userName=sessionStorage.getItem("username");
  },
  updateWord(state,data){
    state.word=data;
  },
  updateWord1(state,data){
    state.word1=data;
  },
  updateLeftNav(state,data){
    state.leftNav=data;
  },
  updateQuestion1(state,data){
    state.question1=data;
  },
  updateQuestion2(state,data){
    state.question2=data;
  },
  updateQuestion3(state,data){
    state.question3=data;
  },
  updateUserJifen(state,data){
    state.userJifen=data;
  },
  updateUserLishiJifen(state,data){
    state.userLishiJifen=data;
  },
  updateUserJifenText(state,data){
    state.userJifenText=data;
  },
  updateWordCloud(state,data){
    state.WordCloud=data;
  },
  updateFaguiNode(state,data){
    state.faguiNode=data;
  },
  updateXinwenNode(state,data){
    state.xinwenNode=data;
  },
  updateFagui(state,data){
    state.faguiList=data;
  },
  updateXinwen(state,data){
    state.xinWenList=data;
  },
  updateFaguiCW(state,data){
    state.faguiCWList=data;
  },
  updateXinwenCW(state,data){
    state.xinWenCWList=data;
  }
  
};
const actions={
  updateLeftNav(context,data){
     reqData
        .req({
          apiName: "leftNav",
          data: {             
            yidian_name: data[0],
            ID:data[1]
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateLeftNav', data);
            sessionStorage.setItem("leftNav", JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });
  },
  updateWord1(context,data){
    sessionStorage.setItem("word1",data);  
    context.commit('updateWord1', data);
  },
  updateQuestion1(context,data){
    reqData
        .req({
          apiName: "showQues",
          data: {   
            qiye_id: data.companyID,          
            yidian_name: data.id,
            feilei_id:'yd-'+data.id2
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateQuestion1', data);
            sessionStorage.setItem('yidianAdd', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });
  },
  updateQuestion2(context,data){
    reqData
        .req({
          apiName: "showQues",
          data: {
            qiye_id:data.companyID,                
            yidian_name: data.id,
            feilei_id:'cw-'+data.id2
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateQuestion2', data);
            sessionStorage.setItem('caiwuAdd', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });
  },
  updateQuestion3(context,data){
    reqData
        .req({
          apiName: "showQues",
          data: {     
            qiye_id:data.companyID,         
            yidian_name: data.id,
            feilei_id:'ch-'+data.id2
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateQuestion3', data);
            sessionStorage.setItem('cihuiAdd', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });
  },
  updateUserJifen(context,data){
    //更新用户积分
      reqData
        .req({
          apiName: "userJifen",
          data: {     
            username:data,         
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateUserJifen', data);
            sessionStorage.setItem('userJifen', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });

      //更新用户历史积分
        reqData
        .req({
          apiName: "userLishiJifen",
          data: {     
            username:data,         
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateUserLishiJifen', data);
            sessionStorage.setItem('userLishiJifen', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });

        //更新用户历史积分记录
        reqData
        .req({
          apiName: "userJifenText",
          data: {     
            username:data,         
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateUserJifenText', data);
            sessionStorage.setItem('userJifenText', JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
       });
  },
  updateWordCloud(context,data){
    let _this=this;
      reqData
        .req({
          apiName: "wordCloud",
          data: {             
            name: data.id
          }
        })
        .then(res => {
          res = res.data;
          if (res && res.code === "200") {
            let data = res.data;
            console.log(data);
            context.commit('updateWordCloud', data);
            sessionStorage.setItem("WordCloud", JSON.stringify(data));     
          }
        })
        .catch(err => {
          console.log(err);
          sessionStorage.setItem("WordCloud", JSON.stringify([]));  
       });
  },
  updateFagui(context,data){
    context.commit('updateFagui', ["null"]);
    context.commit('updateFaguiCW', ["null"]);
    let _this=this;
     reqData
      .req({
        apiName: "faguiList",
        data: {             
          yidian_name:data[0],
          ID:data[1]
        }
      })
      .then(res => {
        res = res.data;
        if (res && res.code === "200") {
          let data = res.data;
          console.log(data);
          context.commit('updateFagui', data);
          sessionStorage.setItem("faguiList", JSON.stringify(data));  
        }
      })
      .catch(err => {
        console.log(err);
      });  


      reqData
      .req({
        apiName: "cwzbList",
        data: {             
          yidian_name:data[0],
          ID:data[1]
        }
      })
      .then(res => {
        res = res.data;
        if (res && res.code === "200") {
          let data = res.data;
          console.log(data);
          context.commit('updateFaguiCW', data);
          sessionStorage.setItem("faguiCWList", JSON.stringify(data));  
        }
      })
      .catch(err => {
        console.log(err);
      });           
  },
  updateXinwen(context,data){
    let _this=this;
    context.commit('updateXinwen', ["null"]);
    context.commit('updateXinwenCW', ["null"]);
    reqData
      .req({
        apiName: "xinwenList",
        data: {             
          name:data[0],
          ID:data[1],
          time:1
        }
      })
      .then(res => {
        res = res.data;
        if (res && res.code === "200") {
          let data = res.data;
          console.log(data);
          context.commit('updateXinwen', data);
          sessionStorage.setItem("xinwenList", JSON.stringify(data));  
        }
      })
      .catch(err => {
        console.log(err);
      });


      reqData
      .req({
        apiName: "cwzbList",
        data: {             
          yidian_name:data[0],
          ID:data[1]
        }
      })
      .then(res => {
        res = res.data;
        if (res && res.code === "200") {
          let data = res.data;
          console.log(data);
          context.commit('updateXinwenCW', data);
          sessionStorage.setItem("xinwenCWList", JSON.stringify(data));  
        }
      })
      .catch(err => {
        console.log(err);
      });
  }
}

const getters = {
  getCompany:state=>{
    return state.company;
  },
  getUser:state=>{
    return state.userName;
  },
  getWordCloud:state=>{
    return state.WordCloud;
  },
  getWord:state=>{
    return state.word;
  },
  getWord1:state=>{
    return state.word1;
  },
  getLeftNav:state=>{
    return state.leftNav;
  },
  getQuestion1:state=>{
    return state.question1;
  },
  getQuestion2:state=>{
    return state.question2;
  },
  getQuestion3:state=>{
    return state.question3;
  },
  getUserJifen:state=>{
    return state.userJifen;
  },
  getUserLishiJifen:state=>{
    return state.userLishiJifen;
  },
  getUserJifenText:state=>{
    return state.userJifenText;
  },
  getFaguiNode:state=>{
    return state.faguiNode;
  },
  getXinwenNode:state=>{
    return state.xinwenNode;
  },
  getFaguiList:state=>{
    return state.faguiList;
  },
  getXinwenList:state=>{
    return state.xinWenList;
  },
  getFaguiCWList:state=>{
    return state.faguiCWList;
  },
  getXinwenCWList:state=>{
    return state.xinWenCWList;
  }
  
};

export default new Vuex.Store({
  // data
   state,
   actions,
   mutations,
   getters

  
});


