<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
  </div>
</template>
<script>
export default {
  name: 'App',
  provide () {
        return {
          reload: this.reload
       }
    },
    data(){
      return{
        isRouterAlive:true
      }
    },
  methods:{
      reload () {
          this.isRouterAlive = false
          this.$nextTick(() => (this.isRouterAlive = true))
        }
    },
}
</script>

<style>
  body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, input, p, blockquote, th, td { margin:0; padding:0; }
  body { width:100%;height:100%;font-size:14px; color:#333; font-family:'微软雅黑'; background:#fff; line-height:22px; }
  fieldset, img { border:0; }
  ol, ul li{ list-style:none; }
  h1, h2, h3, h4, h5, h6{ font-size:100%; }
  em { font-style:normal; }
  input, button, select, textarea { outline:none; }
  textarea { resize:none; } /*禁用了文本的拖拉，尤其在谷歌下*/
  p{ text-align:justify; text-justify:distribute;} /*为了使文本段落左右两边对齐*/
  a { color: #666; text-decoration:none; }
  a:hover { color: #666; text-decoration:none; }
  img { border: none;}
  ::-webkit-scrollbar
  {
    width: 5px;  /*滚动条宽度*/
    height: 8px;  /*滚动条高度*/
  }

  /*定义滚动条轨道 内阴影+圆角*/
  ::-webkit-scrollbar-track
  {
    /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);*/
    border-radius: 10px;  /*滚动条的背景区域的圆角*/
    background-color: #f5f3f3;/*滚动条的背景颜色*/
  }

  /*定义滑块 内阴影+圆角*/
  ::-webkit-scrollbar-thumb
  {
    border-radius: 10px;  /*滚动条的圆角*/
    /*-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);*/
    background-color: #c7c7c7;  /*滚动条的背景颜色*/
  }
  /*.el-input__inner{
    color: #b80000;
    border-color: #b80000;
    background-color: #fff2f2
  }*/
  .el-input__inner:hover,
  .el-input__inner:focus{
        border: 1px solid #b80000;
  }
  .form-input{
    outline: none;
  }
  option:hover{
    background: #b80000;
  }
  .tishi-word{
      color: #cc0000;
      font-size: 16px;
      text-align: center;
      margin-top: -20px;
  }
  .fl {float: left;}
  .fr {float: right;}
  .center{margin: 0 auto;}
  .clear {clear: both; height: 0px; width: 0px;}
  .contain{
    max-width: 1200px;
    margin: 0 auto;
  }
  .over{
    overflow: hidden;
  }
  .hidden{
    display: none;
  }
  .el-icon-search{
    color: #b80000;
  }
  .search-box .el-input__inner{
    border: none;
    height: 40px;
    padding-left: 30px;
    border-top-left-radius: 2em;
    border-bottom-left-radius: 2em;
  }
  .inside .page-top{
    height: 50px;
    margin-top: -70px;
  }
  .inside>.right .content .nav{
    clear: both;
    overflow: hidden;
    width: 100%;
    /*border-bottom: 1px solid #ccc;*/
  }
  .inside>.right .content .nav>ul>li{
    float: left;
   
  }
  .inside>.right .content .nav>ul>li a{
    display: block;
    height: 44px;
    line-height: 44px;
    padding: 0 20px;
    border-radius: 6px;
    min-width: 150px;
    border: 1px solid #ccc;
    background: #fff;
    box-sizing: border-box;
    text-align: center;
    font-size: 16px;
    margin: 0 2px;
  }
  .inside>.right .content .nav>ul>li a.active{   
    color: #fff;
    background-color: #b80000;
    border: 1px solid #b80000;
  }
  .content_page{
    background-color: #fff;
    width: 99%;
    padding:20px;
    margin-top: 16px;
    box-sizing: border-box;
    min-height: 800px;
    background-color: #fff;
  }
  .interactions .modal-outter,.modal-inner{
    border-radius: 8px;
    width: 1100px;
    background-color: #fff;
    text-align: left;
  }
  .interactions .modal-outter>.el-dialog__body{
    height: 660px;
    overflow-y: auto;
    padding: 40px 50px;
  }
  .modal-inner .el-dialog__body{
    height: 660px;
    overflow-y: auto;
  }
  .xqModel .el-dialog__body{
      width: 100%;
      overflow-y: auto;
      padding: 20px;
      max-height: 600px;
     box-sizing: border-box;
    }
    .page4_1 .el-checkbox__label{
      font-size: 16px;
    }
    .page1_2_1 .el-select .el-input ,
    .sb_page2 .el-select .el-input{
      width: 130px;
    }
    .page1_2_1 .input-with-select .el-input-group__prepend {
       background-color: #fff;
    }
    .box-border-shadow{
      box-shadow:0 1px 6px rgba(0,0,0,0.4) 
    }
    .main .search .el-input__inner{
      background-color: transparent;
      color: #fff;
    }
    .el-upload input[type="file"]{
      display: none;
    }
    .page1_2_3 .el-table th > .cell{
      text-align: center;
    }
    .zstp_fg_page1 .el-dialog__title {
      line-height: 44px;
      font-size: 28px;
      color: #303133;
    }
    /*.main .el-autocomplete-suggestion__wrap {
        background-color: rgba(0,0,0,0.1);
        border: 1px solid #e4e7ed;
    }
    .main .el-autocomplete-suggestion li{
      color: #fff;
    }
    .main .el-autocomplete-suggestion li:hover{
      color: #b80000;
    }*/
</style>



